<template>
  <div class="details">
    <div class="main">
      <div class="content">
        <div class="article-title">{{article_info.article_title}}</div>

        <div class="d-fourBlock"></div>

        <div class="info display-flex">
          <span class="type">{{article_info.article_type}}</span>
          <span class="time"><i class="el-icon-date"></i> {{article_info.update_date}}</span>
          <span class="author"><i class="el-icon-edit-outline"></i> {{article_info.author}}</span>
          <span class="classify"><i class="el-icon-folder-opened"></i> {{article_info.article_categories}}</span>
          <span class="view"><i class="el-icon-view"></i> {{article_info.likes}}</span>
        </div>
        <div class="d-fourBlock"></div>
        <div class="thanks">休对故人思故国，且将新火试新茶。诗酒趁年华。</div>
        <div class="d-fourBlock"></div>
        <div class="article-content" v-html="article_info.article_content"></div>


        <div class="d-fourBlock"></div>
        <div class="prev-next display-flex">
          <div class="prev" @click="prevArticle">{{prev_page}}</div>
          <div class="next" @click="nextArticle">{{next_page}}</div>
        </div>
        <div class="finish">完</div>

        <!-- <Discuss :articleInfo="article_info" msg="详情页发送的值"></Discuss> -->



      </div>
    </div>
    <div class="discuss">
      <div class="main">
        <div class="d-title">评论</div>
        <div class="content">
          <div class="discuss-top">
            <el-input v-model.trim="discuss" show-word-limit type="textarea" maxlength="100" placeholder="添加评论,最多100个字.请大家讲文明"></el-input>
            <div class="d-twoBlock"></div>
            <el-button type="primary" size="mini" @click="addComment">评论</el-button>
          </div>
          <div class="discuss-bottom">
            <div class="discuss-inner display-flex" v-for="(item,index) of discuss_group" :key="item.index">
              <div class="discuss-username"><span>{{item.answerer_username}}</span> 说：</div>
              <div class="discuss-commentContent">{{item.comment_content}}</div>
              <div class="discuss-time">{{item.comment_date}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Discuss from '@/components/details/Discuss'
  export default {
    name: 'Details',
    data() {
      return {
        url: '/article',
        article_info: {},
        article_id: 0,
        prev_page: '上一篇',
        next_page: '下一篇',
        discuss: '', //评论
        discuss_group: []
      }
    },
    components: {
      Discuss
    },
    created() {
      this.getArticleId();

      this.queryCommentByArticleId();
    },
    updated() {
    },
    destroyed() {
      this.addLikes();
    },
    methods: {
      // 查询
      getArticleId: function() {
        this.article_id = Number(this.$route.query.article_id);
        this.$axios.get(this.url + '/articleById/' + this.article_id).then(res => {
          if (res.data.code == 200) {
            this.article_info = res.data.body;
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      },
      // 增加阅读量
      addLikes: function() {
        this.$axios.get(this.url + '/updateLikes/' + this.article_info.id).then(res => {
          if (res.data.code == 200) {
            console.log("芜湖~起飞~");
          } else {
            console.log(res.data.message);
          }
        })
      },
      // 获取上一篇
      prevArticle: function() {
        this.article_id = this.article_id + 1;
        this.$axios.get(this.url + '/articleById/' + this.article_id).then(res => {
          if (res.data.code == 200) {
            this.article_info = res.data.body;

            this.addLikes();
            this.discuss_group = [];
            this.queryCommentByArticleId();

          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      },
      // 获取下一篇
      nextArticle: function() {
        this.article_id = this.article_id - 1;
        this.$axios.get(this.url + '/articleById/' + this.article_id).then(res => {
          if (res.data.code == 200) {
            this.article_info = res.data.body;

            this.addLikes();
            
            this.discuss_group = [];
            this.queryCommentByArticleId();

          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      },

      // 添加评论
      addComment: function() {
        if (this.$store.state.login_phone != '' || this.$store.state.login_name != '') {
          let data = {
            articleId: this.article_info.id,
            original_author: this.article_info.author,
            answerer_phone: this.$store.state.login_phone,
            answerer_username: this.$store.state.login_name,
            comment_content: this.discuss
          };
          this.$axios.post('/comment/addComment', data).then(res => {
            if (res.data.code == 200) {
              this.$message({
                message: res.data.message,
                type: 'success'
              })

              this.queryCommentByArticleId();
              this.discuss = "";

            } else {
              this.$message({
                message: res.data.message,
                type: 'warning'
              })
            }
          })
        } else {
          this.$message.error('请先登录再评论哦')
          this.$router.push('/login');
        }
      },
      // 获取评论
      queryCommentByArticleId: function() {
        this.$axios.get('/comment/queryCommentByArticleId/' + this.article_id).then(res => {
          if (res.data.code == 200) {
            this.discuss_group = res.data.body;
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      }

    }
  }
</script>

<style scoped>
  .article-content>>>img {
    max-width: 100%;
    margin: 40px auto;
  }

  .article-content>>>p {
    width: 100%;
  }

  .main {
    width: 80%;
    margin: 0 auto;
  }

  .article-title {
    text-align: center;
    font-size: 24px;
  }

  .info span {
    display: block;
    margin-right: 10px;
    padding: 3px 5px;
    color: #909399;
  }

  .info span:nth-child(1) {
    background-color: #409EFF;
    color: #fff;
    border-radius: 5px;
  }

  .desc {
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 1px;
    color: #606266;
  }

  .article-content {
    width: 100%;
    margin: 0 auto;
    background-color: #f9f9f9;
    box-sizing: border-box;
    padding: 20px;
  }

  .thanks {
    width: 70%;
    margin: 0 auto;
    text-align: center;
    background-color: #aed9d3;
    font-family: "楷体";
    color: #606266;
    padding: 20px 0;
    font-size: 16px;
    border-left: 3px solid #67C23A;
    border-right: 3px solid #67C23A;
    box-sizing: border-box;
  }

  .finish {
    width: 50px;
    height: 50px;
    margin: 0 auto;
    text-align: center;
    line-height: 50px;
    border-radius: 50%;
    font-size: 18px;
    font-weight: bold;
    color: #909399;
    background-color: #f9f9f9;
  }

  img {
    width: 100%;
  }

  .prev-next {
    width: 100%;
    justify-content: space-between;
    padding: 20px 0;
  }

  .prev-next>div {
    cursor: pointer;
  }

  .prev-next>div:hover {
    color: #409EFF;
  }




  .discuss {
    padding-top: 60px;
  }

  .discuss-bottom {
    padding-top: 40px;
  }

  .discuss-inner {
    position: relative;
    margin-bottom: 40px;
  }

  .discuss-username {
    width: 15%;
    font-size: 14px;
  }

  .discuss-username span {
    color: #E6A23C;
  }

  .discuss-commentContent {
    width: 75%;
    background-color: #f9f9f9;
    box-sizing: border-box;
    padding: 5px 10px;
    font-size: 14px;
    color: #67C23A;
  }

  .discuss-time {
    position: absolute;
    font-size: 10px;
    color: #409EFF;
    top: 0;
    right: 0;
  }
</style>



