<template>
  <div class="discuss">
    <div class="main">
      <div class="d-title">评论</div>
      <div class="content">
        <el-input v-model.trim="discuss" show-word-limit type="textarea" maxlength="100" placeholder="添加评论,最多100个字"></el-input>
        <div class="d-twoBlock"></div>
        <el-button type="primary" size="mini">评论</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Discuss',
    props: {
      'articleInfo': Object,
      'msg': String
    },
    data() {
      return {
        discuss: ''
      }
    },
    created() {
      console.log(this.articleInfo);
      console.log(this.msg)
    }
  }
</script>

<style scoped>
  .discuss {
    padding-top: 60px;
  }

  .el-input {
    width: 50%;
  }
</style>



